.place-card__delete-icon {
    background: url(../../../images/trash-icon-transp.svg) center no-repeat;
    background-color: transparent;
    border: none;
    width: 18px;
    height: 20px;
    position: absolute;
    top: 18px;
    right: 15px;
    cursor: pointer;
    display: none;
}

.place-card__delete-icon:enabled {
    display: block;
}

.place-card__delete-icon:enabled:hover {
    background: url(../../../images/trash-icon.svg) center no-repeat;
}
